const { ethers } = require("ethers");
const BigNumber = require('bignumber.js');
const Web3 = require('web3');

BigNumber.config({ EXPONENTIAL_AT: 60 });

const bscUrl = "https://matic-mumbai.chainstacklabs.com";

const methodSwitchEthTestnet = {
    method: 'wallet_switchEthereumChain',
    params: [{ chainId: '0x13881' }],
};

let provider;
let viewProvider;

async function connect() {
    if (typeof window.ethereum !== "undefined") {
        try {
            const { ethereum } = window;
            await ethereum.request({ method: "eth_requestAccounts" });
            provider = new ethers.providers.Web3Provider(window.ethereum);
            const { chainId } = await provider.getNetwork();
            if ((chainId !== 80001)) {
                await ethereum.request(methodSwitchEthTestnet);
            }
            return true;
        } catch (error) {
            console.log(error);
            return false;
        }
    } else {
        console.log("Please install MetaMask");
        return false;
    }
}


const connectNode = () => {
    try {
        const provider = new Web3.providers.HttpProvider(bscUrl)
        viewProvider = new Web3(provider)
        return true
    } catch (e) {
        return false
    }
}

async function checkAddress() {
    const { ethereum } = window;
    const accounts = await ethereum.request({ method: "eth_accounts" });
    return accounts.length;
} 

async function getFirstAddress() {
    const { ethereum } = window;
    const accounts = await ethereum.request({ method: "eth_accounts" });
    return accounts[0];
}

const fetchContractData = async (abi, addressContract ,method, params) => {
    if(!viewProvider) {
        connectNode()
    }
    try {
        const contract = new viewProvider.eth.Contract(abi, addressContract)
        return await contract.methods[method].apply(this, params).call()
    } catch (e) {
        console.log(e)
        return ''
    }
} 

const sendDataToContract = async (abi, addressContract, method, params) => {
    const signer = provider.getSigner();
    const contract = new ethers.Contract(addressContract, abi, signer);
    try {
        const tx = await contract[method].apply(this, params)
        return tx;
    } catch (error) {
        console.log(error);
        return error;
    }
}

module.exports = {
    connect,
    connectNode,
    checkAddress,
    fetchContractData,
    sendDataToContract,
    getFirstAddress
};