import Vue from 'vue'
import Vuex from 'vuex'
import {
  connect,
  checkAddress,
  sendDataToContract
} from '@/utils/web3.js';

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
      loader: false,
      isConnected: false,
      message: '',
      memberProposals: [],
      oracleProposals: [],
      notification: '',
      showOK: false
    },
    getters: {
      IS_CONNECTED: state => state.isConnected,
      MESSAGE: state => state.message,
      LOADER: state => state.loader,
      NOTIFICATION: state => state.notification,
      SHOW_OK: state => state.showOK
    },
    mutations: {
      SET_CONNECT_STATUS: (state, status) => state.isConnected = status, 
      SET_LOADER_STATUS: (state, status) => state.loader = status, 
      SET_MESSAGE: (state, msg) => state.message = msg,
      SET_NOTIFICATION: (state, str) => state.notification = str,
      SET_SHOW_OK: (state, status) => state.showOK = status,
    },
    actions: {
      SET_LOADER_STATUS({commit}, status) {
        commit('SET_LOADER_STATUS', status);
      },
      async CONNECT_METAMASK({commit}) {
        commit('SET_LOADER_STATUS', true);
        const status = await connect();
        if(status) {
          commit('SET_CONNECT_STATUS', true);
        }
        commit('SET_LOADER_STATUS', false);
      },   
      async SEND_DATA({commit}, data) {
        const addresses = await checkAddress();
        if(addresses) {
          await sendDataToContract(data.abi, data.addressContract, data.method, data.params);
          // if (message.hash) {
          //   commit('SET_MESSAGE', `https://goerli.etherscan.io/tx/${message.hash}`);
          // } else if (message.error.message) {
          //   commit('SET_MESSAGE', message.error.message);
          // }
        } else {
          commit('SET_CONNECT_STATUS', false);
        }
      },
    },
    modules: {
    }
  })