<template>
    <div class="preloader">
        <div class="preloader__loader">
        </div>
        <h4 class="preloader__msg">{{ NOTIFICATION }}</h4>
        <!-- <h4 class="preloader__msg">`erfherbvejkrhgu rekjudygbtsrlt ruithbgvwrthdgv wieuthgbvwrt gwiutehbgvw ttgiwuthgwpiurthgv wrtpirughh utreignw </h4> -->
        <button @click="handleOK()" v-if="SHOW_OK" class="btn btn-warning btn-lg preloader__btn">OK</button>
    </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'

export default {
    name: "TwistLoader",
    methods: {
        ...mapMutations(['SET_SHOW_OK','SET_NOTIFICATION','SET_LOADER_STATUS']),
        handleOK() {
            this.SET_NOTIFICATION('');
            this.SET_LOADER_STATUS(false);
            this.SET_SHOW_OK(false);
        }
    },
    computed: {
        ...mapGetters(['NOTIFICATION','SHOW_OK'])
    }
}
</script>

<style scoped>
.preloader {
    background-color:azure;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
}
.preloader__loader {
    width: 75px;
    height: 75px;
    border: 10px solid black;
    border-radius: 50%;
    border-top-color: blueviolet;
    border-bottom-color: blueviolet;
    position: absolute;
    left: 50%;
    top: 40%;
    transform: translate(-50%, -50%);
    animation: 2s spin infinite linear;
}
.preloader__btn {
    position: absolute;
    left: 50%;
    top: 65%;
    transform: translate(-50%, -50%);
}

.preloader__msg {
    text-align: center;
    width: 600px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

@keyframes spin {
    from {
        transform: translate(-50%, -50%) rotate(0deg);
    }
    to {
        transform: translate(-50%, -50%) rotate(360deg);
    }
}
</style>